import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return {
            list:null,
            drMsg:this.$store.state.userInfo.Data.User,
            temp:false,
			professionalLevel:null,
			fTemp:false
        }
    },
    created(){
        this.getList()
    },
    mounted(){
    },
    methods:{
        getList(){
            this.$get('PlatFormAPI/Doctor/QueryDoctorInfoByDrId',{
                DrId: this.drMsg.DoctorId
            }).then(o => {
                this.professionalLevel = o.ProfessionalLevel
            })
			this.$get('PlatFormAPI/SpecialistHosGroup/ExistSpecialistHosGroupMember',{
			    DrId: this.drMsg.DoctorId
			}).then(o => {
			    if(o){
					this.temp = true
				}
			})
			this.$get('PlatFormAPI/DoctorReferral/ExistDoctorDisGroup',{
			    DrId: this.drMsg.DoctorId
			}).then(o => {
			    if(o){
					this.fTemp = true
				}
			})
        },
        goTo(){
			if(!this.fTemp){
				this.$router.push('/gradeCoop')
				return
			}
			if(this.professionalLevel == 1){
				this.$router.push('/drDieaseList')
			}else{
				this.$router.push('/dieaseList?fromSet=1')
			}
        	
        },
        
    }
}



